export default {
  type: String,
  // 
  isInternational: {
    type: Boolean
  },
  value: {
    type: Object,
    default: () => {}
  },
  // 
  size: {
    type: String,
    default: 'medium',
    validator: function (value) {
      return ['medium', 'small', 'mini'].indexOf(value) !== -1
    }
  },
  // 
  inline: {
    type: Boolean,
    default: false
  },
  // 
  formList: {
    type: Array,
    required: true,
    default: () => []
  },
  // 
  labelPosition: {
    type: String,
    default: 'right'
  },
  //  Form  form-item 
  labelWidth: {
    type: Number,
    default: 80
  },
  // 
  labelSuffix: {
    type: String
  },
  // 
  statusIcon: {
    type: Boolean,
    default: true
  },
  // 
  inlineMessage: {
    type: Boolean,
    default: false
  },
  // column 
  isColumnMixins: {
    type: Boolean,
    default: false
  },
  // 
  showMessage: {
    type: Boolean,
    default: true
  },
  disabled: {
    type: Boolean,
    default: false
  },
  validateOnRuleChange: {
    type: Boolean,
    default: true
  },
  submitBtn: {
    type: Object,
    default: () => {}
  },
  dicData: {
    type: Object,
    default: () => {}
  },
  dicUrl: String,
  searchSize: {
    type: String,
    validator: function (value) {
      return ['medium', 'small', 'mini'].indexOf(value) !== -1
    }

  },
  resetBtn: {
    type: Object,
    default: () => {}
  },
  searchBtn: {
    type: Object,
    default: () => {}
  },
  //  form
  formFormat: Function,
  // 
  handlerSubmit: Function
}
