//  (method)
export const getInternationalValue = function (value) {
  if (typeof value !== 'string') {
        console.error('') /*eslint-disable-line*/
    return false
  }

  if (this.international && this.$te(value)) {
    return this.$t(value)
  } else {
    return value
  }
}
export function isExist (a) {
  return a !== null && a !== undefined
}
