const toString = Object.prototype.toString
export function isFunction (obj) {
  return toString.call(obj) === '[object Function]'
}
export function isExist (a) {
  return a !== null && a !== undefined
}
/**
 *
 *
 * @param {*} o1 o2 
 * @returns
 */
export function deepEqual (o1, o2) {
  if (typeof o1 !== 'object' || typeof o2 !== 'object') { return o1 === o2 }
  //  o1, o2  o1 o2 o1
  for (var p in o1) {
    if (!deepEqual(o1[p], o2[p])) { return false }
  }
  //  o2 o1
  for (var q in o2) {
    if (!(q in o1)) { return false }
  }
  return true
}
// 
export const isInArr = function (arr, item) {
  let flag = -1
  for (let i = 0; i < arr.length; i++) {
    if (deepEqual(arr[i], item)) {
      flag = i
      break
    } else {
      flag = -1
    }
  }
  return flag
}

/**
 *
 * @export
 * @param {*} obj
 * @returns
 */
export function isEmptyObject (obj) {
  if (obj && typeof obj !== 'object') {
        console.warn('') /*eslint-disable-line*/
    return
  }
  return Object.keys(obj).length === 0
}
/**
 *JSON
 * @export
 * @param {*} str
 * @returns
 */
export function isJsonString (str) {
  if (typeof str === 'string') {
    try {
      JSON.parse(str)
      return true
    } catch (e) {
      return false
    }
  }
}
/**
 * 
 * obj obj.data.list.page
 * getValueByPath(obj,'data.list.page')
 * @param {*} object   
 * @param {*} prop  
 */
export const getValueByPath = function (object, prop) {
  prop = prop || ''
  const paths = prop.split('.')
  let current = object
  let result = null
  for (let i = 0, j = paths.length; i < j; i++) {
    const path = paths[i]
    if (!current) break

    if (i === j - 1) {
      result = current[path]
      break
    }
    current = current[path]
  }
  return result
}
